import { Component } from '@angular/core';
import { AuthService } from '../app/shared/shared-services/auth/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  cookieEmail: any;
  email: any;
  constructor(
    private _auth: AuthService,    private _router: Router

  ) {
    this.checkCookie();
    this.email= this.cookieEmail.replace(/['"]+/g,'');
    if(this.email == null || this.email == ""){

      this._auth.getclientDetails().subscribe((data: any) => {
        // console.log(data);
        sessionStorage.setItem('clientDetails', JSON.stringify(data));
        console.log("session")
        this._auth.setAppFavicon('appIcon', data.browserTitle, data.favIcon);
        this._auth.getUserInfo().subscribe((data) => {
          localStorage.setItem('_u', JSON.stringify(data));
          this.gotoDashboard();
        });
      }, (error) => {
        console.log(error);
      });
      // window.location.assign("http://192.168.99.100:8000/saml/sso");
    }
    else{
      this._auth.getclientDetails().subscribe((data: any) => {
        // console.log(data);
        sessionStorage.setItem('clientDetails', JSON.stringify(data));
        console.log("session")
        this._auth.setAppFavicon('appIcon', data.browserTitle, data.favIcon);
        this._auth.getUserInfo().subscribe((data) => {
          localStorage.setItem('_u', JSON.stringify(data));
          this.gotoDashboard();
        });
      }, (error) => {
        console.log(error);
      });
    }
    
  }
  private gotoDashboard() {
    this._router.navigate(['/app/operational/dashboard']);
  }

  getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  checkCookie() {
    this.cookieEmail = this.getCookie("email");
  }

}




